body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: -webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-image: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
}
button{
  margin: 30px;
  padding: 20px;
  border: 2px solid black;
  background-color: green;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: inset 0 0 1em gold, 0 0 1em red;
  cursor: pointer;
  will-change: transform;
  -webkit-transition: -webkit-transform 1s ease-out;
  transition: -webkit-transform 1s ease-out;
  transition: transform 1s ease-out;
  transition: transform 1s ease-out, -webkit-transform 1s ease-out;
  border-radius: 20%;
}
button:hover{
  background-color: rgb(27, 192, 27);
  box-shadow: inset 0 0 1em rgb(224, 192, 10), 0 0 1em rgb(233, 7, 7);
  -webkit-transform:  translateY(15px);
          transform:  translateY(15px);
}


.users{
  text-align: center;
  list-style-type: none;
  padding: 30px;
  background-image: -webkit-linear-gradient(bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-image: linear-gradient(to top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
}
img{
  border-radius: 10px;
}
h4{
  text-transform: uppercase;
}

